.backdrop {
  position: absolute;
  z-index: -1;
  top: 0px;
}

.image {
  max-width: 280px;
  height: 130px;
  width: 50%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  margin-bottom: 65px;
}

@media(min-width: 561px) {
  .image { height: 150px }
}

@media(min-width: 561px) and (max-width: 840px) {
  .image {
    width: 33.33%;
  }
}

@media(min-width: 841px) and (max-width: 1120px) {
  .image {
    width: 25%;
  }
}

@media(min-width: 1121px) and (max-width: 1400px) {
  .image {
    width: 20%;
  }
}

@media(min-width: 1401px) and (max-width: 1680px) {
  .image {
    width: 16.66%;
  }
}

@media(min-width: 1681px) and (max-width: 1960px) {
  .image {
    width: 14.285%;
  }
}

@media(min-width: 1961px) and (max-width: 2240px) {
  .image {
    width: 12.5%;
  }
}

@media(min-width: 2241px) and (max-width: 2520px) {
  .image {
    width: 11.111%;
  }
}

@media(min-width: 2521px) and (max-width: 2800px) {
  .image {
    width: 10%;
  }
}

@media(min-width: 2801px) and (max-width: 3080px) {
  .image {
    width: 9.091%;
  }
}

@media(min-width: 3081px) and (max-width: 3361px) {
  .image {
    width: 8.333%;
  }
}




